* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.title {
    display: flex;
    justify-content: center;
    margin: 2% 0;
}

.center {
    display: flex;
    justify-content: center;
}

.navbar {
    height: 20%;
}

.nav {
    border-right: solid 1px rgb(58, 58, 58);
}

.logo {
    width: 100%;
    max-width: 12%;
}

.infopush {
    border-bottom: dotted 2px rgb(121, 119, 119);
    width: 100%;
    max-width: 33%;
    margin: 30px;
}

.pushinput {
    background-color: #fff !important;
    height: 30px !important;
    width: 100%;
    max-width: 100px;
    border: 1px solid rgb(107, 107, 107) !important;
}

.functions {
    border: 1px solid rgb(121, 121, 121);
    background-color: rgb(218, 216, 216);
}

.arrays {
    border: solid 1px black;
    width: 100%;
    max-width: 74px;
    height: 80px;
    font-size: 2.4rem;
    text-align: center;
    margin: 20px;
}

.arrlist {
    display: flex;
    margin: 20px;
}